
import Loading from 'vux/src/components/loading/index.vue'

import {mapState,mapActions, mapMutations} from 'vuex'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default {
directives: {
TransferDom
},
components:{
Loading,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem
},
name: 'App',
data(){
return {
showMenus:false,
menus:{
menu1:'第一个选项',
menu2:'第二个选项'
},
transitionName:'slide-left'
}
},
computed:{
...mapState(['loadState','loadText']),
...mapState('nav',['navBar','showBack','showMore','navTitle'])
},
mounted(){
this.$router.beforeEach((to,from,next)=>{
this.setnavTitle(to.meta.title)
console.log(to.path)
if(to.path.length > 1){
this.setnavBar(true)
this.setshowBack(true)
this.setshowMore(true)
}else {
this.setnavBar(true)
this.setshowBack(false)
this.setshowMore(false)

}
next()
})
},
methods:{
...mapMutations(['setLoadText']),
...mapMutations('nav',['setnavBar','setshowBack','setshowMore','setnavTitle'])
}
}



