
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'

import {mapState,mapActions, mapMutations} from 'vuex'
import moviePng from '../assets/imgs/movie.png'
import musicPng from '../assets/imgs/music.png'
import booksPng from '../assets/imgs/books.png'
import activityPng from '../assets/imgs/activity.png'
export default {
components :{
Grid,
GridItem,
GroupTitle
},
data(){
return {
grids:[
{
name:'电影',
icon:moviePng,
link:'/movie'
},
{
name:'音乐',
icon:musicPng,
link:'/music'
},
{
name:'读书',
icon:booksPng,
link:'/book'
},
{
name:'活动',
icon:activityPng,
link:'/activity'
},
]
}
},
created(){
this.setnavBar(true)
this.setshowBack(false)
this.setshowMore(false)
this.setnavTitle('柒年')
},
computed:{

},
methods:{
...mapMutations('nav',['setnavBar','setshowBack','setshowMore','setnavTitle'])
},

}



